.second-screen ,.modal-servises {
	padding-top: 80px;
	position: relative;
	width: 100%;
	min-height: 884px;
	margin-left: -15px;
	background: url("../img/bg-services.png") -15% no-repeat;
	background-size: cover;
	margin-right: -15px;
	@include r(1199) {
		margin-right: 0;
		margin-left: 0; }
	.services {
		display: block;
		background-color: rgb(0, 36, 124);
		margin-right: 10px;
		color: #ffffff;
		@include r(1199) {
			margin-right: 0; }
		&__block {
			display: flex;
			width: 100%;
			@include r(545) {
				flex-direction: column;
				align-items: center; }
			@include r(430) {
				align-items: start;
				padding-left: 20px;
				padding-right: 20px; }
			&_left,&_right {
				width: 50%;
				margin: 87px 0;
				@include r(545) {
					margin: 10px 0; } }
			&_left {
				margin-left: 60px;
				margin-right: 10px;
				@include r(767) {
					margin-left: 10px; } }
			&_right {
				margin-right: 60px;
				margin-left: 10px;
				@include r(767) {
					margin-right: 10px; } } }
		&__elem {
			padding: 22px 20px;
			width: 250px;
			margin-bottom: 11px;
			&:last-child {
				margin-bottom: 0; }
			@include r(430) {
				width: 325px; }
			@include r(370) {
				width: 280px; }
			img {
				width: 45px;
				height: 45px;
				margin-bottom: 15px; }
			p {
				font-size: 20px;
				line-height: 22px;
				font-family: $medium-font;
				letter-spacing: -0.01em;
				margin-bottom: 3px;
				&:last-child {
					font-size: 16px;
					line-height: 20px;
					font-family: $defoult-font;
					color: #7b818a; } }
			&:hover, &:focus {
				background: #ffffff;
				border-radius: 8px;
				p {
					color: $main-color;
					&:last-child {
						color: #7b818a; } } } } }
	.services-text {
		padding-top: 106px;
		margin-left: 40px;
		@include r(1199) {
			margin-left: 0;
			padding-top: 50px;
			padding-bottom: 10px; }
		@include r(575) {
			padding: 20px 20px; }
		h3 {
			font-size: 36px;
			line-height: 39px;
			letter-spacing: -0.01em;
			font-family: $light-font;
			font-weight: 100;
			@include r(575) {
				font-size: 24px; } }
		strong {
			font-size: 24px;
			font-family: $light-itali-font;
			font-weight: 100;
			color: #7b818a;
			line-height: 40px;
			letter-spacing: -0.012em;
			position: relative;
			&::before {
				background: url("../img/sea-water.svg") no-repeat center;
				content: '';
				width: 65px;
				height: 20px;
				position: absolute;
				bottom: -32px; }
			@include r(575) {
				font-size: 20px; } }
		p {
			margin-top: 60px;
			font-size: 16px;
			line-height: 30px;
			color: #7b818a;
			letter-spacing: -0.01em; } } }

.modal-servises {
	background: #ffffff;
	width: 90%;
	margin: 0;
	left: 50%;
	transform: translateX(-50%);
	padding: 15px;
	min-height: auto;
	@include r(430) {
		width: 100%; }
	.close-servises {
		font-size:  50px;
		color: red;
		position: absolute;
		right: 20px;
		top: 0;
		@include r(425) {
			display: none; } }
	.services-text {
		padding-top: 50px; }
	.services {
		&__elem {
			position: relative;
			@include r(430) {
				width: 300px; }
			@include r(370) {
				width: 230px; } } }
	&__button {
		margin: 30px auto;
		width: 300px;
		@include r(425) {
			width: 190px; } } }
