.first-screen {
	min-height: 570px;
	width: 100%;
	background: url("../img/bnr.png") no-repeat center;
	background-size: cover;
	padding-top: 107px;
	position: relative;
	text-align: center;
	@include r(768) {
		padding-top: 20px;
		min-height: auto; }
	h1 {
		font-size: 36px;
		color: $main-color;
		margin-bottom: 37px;
		@include r(768) {
			font-size: 30px; }
		@include r(425) {
			font-size: 24px; } }
	h2 {
		font-size: 24px;
		line-height: 36px;
		letter-spacing: 0.01em;
		color: #6a707a;
		margin-bottom: 115px;
		@include r(768) {
			font-size: 20px;
			line-height: 26px;
			margin-bottom: 50px; }
		@include r(530) {
			font-size: 18px; }
		@include r(480) {
			font-size: 16px; }
		@include r(425) {
			font-size: 14px; } }
	&__buttons {
		display: flex;
		justify-content: center;
		margin-bottom: 50px;
		@include r(767) {
			flex-direction: column;
			align-items: center; }
		a {
			border-radius: 24px;
			background-color: rgb(3, 166, 241);
			box-shadow: 10px 17.321px 30px 0 rgba(73, 175, 236, 0.3);
			width: 270px;
			height: 48px;
			padding: 14px;
			font-size: 15px;
			font-family: $bold-font;
			letter-spacing: 0.006em;
			color: #fefefe;
			margin: 0 16px;
			transition: all .3s;
			&:last-child {
				background-color: rgb(0, 36, 124);
				box-shadow: 10px 17.321px 30px 0 rgba(0, 36, 124, 0.3); }
			&:hover {
				transform: translateY(3px);
				box-shadow: 10px 17.321px 30px 0 rgba(73, 175, 236, 0.6);
				&:last-child {
					box-shadow: 10px 17.321px 30px 0 rgba(0, 36, 124, 0.6); } }
			@include r(767) {
				margin-bottom: 25px; } } }
	&__nav {
		color: $main-color;
		@include r(767) {
			display: none; }
		li {
			margin: 0 auto;
			text-align: left;
			position: relative;
			padding-left: 13px;
			transition: all .3s;
			&::before {
				content: '';
				width: 5px;
				height: 5px;
				position: absolute;
				background: $main-color;
				border-radius: 3px;
				top: 7px;
				left: 0; } }
		.slick-current {
			color: #03a6f1;
			li {
				&::befor {
					background: #03a6f1; } } } } }
