.pulse {
  position: relative;
  left: 50%;
  top: 50vh;
  margin: -40px 0 0 -40px;
}

.pulse::before, .pulse::after {
  content: '';
  border: 5px solid #2c3544;
  height: 80px;
  width: 80px;
  border-radius: 50%;
  position: absolute;
}

.pulse::before {
  animation: pulse-outer .8s ease-in infinite;
}

.pulse::after {
  animation: pulse-inner .8s ease-in infinite;
}

@keyframes pulse-outer {
  0% {
    opacity: .1;
  }
  50% {
    opacity: .5;
  }
  100% {
    opacity: 0;
  }
}

@keyframes pulse-inner {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

