.about-us {
	width: 100%;
	min-height: 920px;
	background: url("../../img/about-us-bg.png") top center no-repeat fixed;
	background-size: cover;
	@include r(992) {
		padding-bottom: 50px;
		min-height: auto; }
	@include r(600) {
		padding-bottom: 0; }

	&__logo-block {
		width: 340px;
		height: 340px;
		background-color: #fff;
		margin-left: 104px;
		padding: 145px 94px;
		@include r(991) {
			display: none; } }
	&__info {
		padding-left: 71px;
		padding-top: 143px;
		@include r(1199) {
			padding-left: 0; }
		@include r(992) {
			padding-top: 20px; }
		@include r(600) {
			padding: 20px; }
		p {
			font-size: 18px;
			line-height: 30px;
			margin-bottom: 31px;
			&:nth-child(3) {
				margin-bottom: 155px; }
			@include r(600) {
				font-size: 16px; } } } }
